
// CSS
import '../assets/css/pages/contact-us.css';

// Images

import IcPhoneLg from "../assets/images/icons/ic-phone-lg.svg"
import IcEmailLg from "../assets/images/icons/ic-email-lg.svg"
import IcMapLg from "../assets/images/icons/ic-map-lg.svg"

import ImgContactUs from '../assets/images/backgrounds/conatctus-media.svg';

function ContactUs() {
    return (
        <div className="main-content">
            <section className="conatct-us">
                <div className="container">
                    <div className="section-title varient-two">
                        <h4>Contact us</h4>
                    </div>
                    <div className="row">
                        <div className="col-lg-7">
                            <div className="contact-form-box">
                                <form action="">
                                    <div className='title'>
                                        <h4>Send us a message</h4>
                                        <p>How can we help you?</p>
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="full_name" className="form-label">Full Name</label>
                                        <input type="text" className="form-control" id="full_name" placeholder="Enter Your Full Name" />
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="phone_number" className="form-label">Phone Number</label>
                                        <input type="text" className="form-control" id="phone_number" placeholder="Enter Your Phone Name" />
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="message" className="form-label">Message</label>
                                        <textarea className="form-control" id="message" rows="3" placeholder="Write here..."></textarea>
                                    </div>
                                    <div className="row">
                                        <div className='col-lg-6'>
                                            <button className='button button-blue w-100'>Submit</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <div className="col-lg-5">
                            <div className='contact-us-info light-blue-bg'>
                                <div className='contact-info-metas'>
                                    <div className='with-icon'>
                                        <img src={IcPhoneLg} alt="" />
                                        <h4>Toll free no</h4>
                                        <p>+964 989 1636 548</p>
                                    </div>
                                    <div className='with-icon'>
                                        <img src={IcEmailLg} alt="" />
                                        <h4>Email</h4>
                                        <p>inannaa123@gmail.com</p>
                                    </div>
                                    <div className='with-icon'>
                                        <img src={IcMapLg} alt="" />
                                        <h4>Address</h4>
                                        <p>Ankawa Boulevard 128 Road, Erbil</p>
                                    </div>
                                </div>
                                <div className='contact-us-media'>
                                <img src={ImgContactUs} alt="" />
                            </div> 
                            </div>
                            
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default ContactUs;